/** Code formatting */
/* Applies to all code */
pre,
code {
  font-family: "Hack", monospace;
  font-style: normal;
  font-feature-settings: "kern" off;
  font-kerning: none;
  font-variant: none;
  background-color: #eef;
}

pre ::selection,
code ::selection {
  color: #1a1a1a;
  background: #bdbdb3;
}

pre ::-moz-selection,
code ::-moz-selection {
  color: #1a1a1a;
  background: #bdbdb3;
}

pre ::-webkit-selection,
code ::-webkit-selection {
  color: #1a1a1a;
  background: #bdbdb3;
}

/* Inline code, no highlight */
code {
  padding: 2px 4px;
  border-radius: 3px;
  white-space: nowrap;
  font-size: 0.9rem;
}

blockquote code {
  color: #5c5c5c;
}

/* Inline code, highlight */
code.highlight {
  border-radius: 3px;
  color: #bdbdb3;
  background-color: #1a1a1a;
}

/* Block code, no highlight */
pre {
  border-radius: 5px;
  margin: 22.5px 15px;
  padding: 4px 6px;
  overflow-x: auto;
  background-color: #f1f1ff;
}

pre code {
  border: 0;
  padding: 0;
  white-space: pre;
}

/* Block code, highlight */
figure.highlight,
div.highlight {
  position: relative;
}

figure.highlight pre,
div.highlight pre {
  background-color: #1a1a1a;
}

figure.highlight pre code,
div.highlight pre code {
  background-color: #1a1a1a;
  color: #bdbdb3;
  counter-reset: code;
  display: block;
  line-height: 1.5; /* Display language name */
}

figure.highlight pre code[data-lang]::before,
div.highlight pre code[data-lang]::before {
  content: attr(data-lang);
  position: absolute;
  right: 34px;
  top: -10px;
  padding-left: 7.5px;
  padding-right: 7.5px;
  border: 1px solid #828282;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  min-width: 40px;
  text-align: center;
  color: #8fbc8f;
  background-color: #1a1a1a;
}

@media screen and (max-width: 300px) {
  figure.highlight pre code[data-lang]::before,
  div.highlight pre code[data-lang]::before {
    visibility: hidden;
  }
}

pre {
  margin: auto;
}

figure.highlight {
  margin: 0;
  padding: 0;
}

.infoModule {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-bottom: $blockThree;

  li {
    list-style-type: none;
    margin: 0;
    display: inline-block;
    // background-color: rgba(128, 128, 128, 0.1);
    // padding: 2px 12px 2px 12px;
    // border-radius: 4px;
  }

  li:nth-child(n + 3):before {
    content: $symbol;
  }

  h4 {
    margin-bottom: $blockTwo;
    opacity: 0.55;
  }
  > div {
    border-bottom: solid 1px;
    padding: 0 $blockThree $blockThree 0;
  }
  > div:nth-child(even) {
    padding-left: $blockThree;
    border-left: solid 1px;
  }
  > div:nth-child(3),
  div:nth-child(4) {
    padding-top: $blockThree;
    border-bottom: solid 5px;
  }
}
